/* checkbox */
.checkbox-remember-me {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items:center;
            align-items:center;
    position: relative;
    padding-left: 25px;
    margin-bottom: 12px;
    cursor: pointer;
    width: 70%;
    color: white;
    font-size: 14px;
}

.checkbox-remember-me input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 15px;
    width: 15px;
    background-color: black;
    border: 1px solid white;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  .checkbox-remember-me input:checked ~ .checkmark:after {
    display: block;
  }
  
  .checkbox-remember-me .checkmark:after {
    left: 30%;
    top: 15%;
    width: 5px;
    height: 6px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }

